<template>
    <div  class="info-div" style="100%">
        <div class="info-header">
          <span class="callback" @click="resetComponent">缴费记录 </span>
          <span> > 缴费详情</span>
        </div>
        <VuePerfectScrollbar class="info-container" v-if="show" style="">
            <div v-for="(item, $index) in formOpt" class="list-content detail-content  list-contentB" >
              <div class="list-header" style="margin-bottom:0.7em">
                <i class="hasIcon" :class="{'hasImg': item.img}">
                  <icon :name="item.icon" v-if="item.icon"></icon>
                  <img :src="item.img" v-if="item.img" />
                 </i>
                {{item.name}}
                <template v-if="item.operate  && paymentList !== '' && paymentList.isEnabled == 1" v-for="(btn, $index) in item.operate">
                  <!--v-if="paymentList.isValid != 0 && ((btn.controls !== -100 && controls[btn.controls]) || btn.controls === -100) "-->
                  <template v-if= "btn.controls == 15">
                  	  <button  class="list-operate" @click="btnEvent(btn.event)" v-if=" selected.isRepeatCard === 1 &&  paymentList.isEnabled === 1 && paymentList.isValid != 0 && ((btn.controls !== -100 && controls[btn.controls]) || btn.controls === -100)">
                    <i class="hasIcon" :class="{'hasImg': btn.img}">
                      <icon :name="btn.icon" v-if="btn.icon"></icon>
                      <img :src="btn.img" v-if="btn.img" />
                    </i>
                    {{btn.text}}
                  </button>
                  </template>
                  <template v-else>
                  	 <button  class="list-operate" @click="btnEvent(btn.event)" v-if=" selected.isRepeatCard === 1 && paymentList.isEnabled === 1 && paymentList.isValid != 0 && ((btn.controls !== -100 && controls[btn.controls]) || btn.controls === -100)">
                    <i class="hasIcon" :class="{'hasImg': btn.img}">
                      <icon :name="btn.icon" v-if="btn.icon"></icon>
                      <img :src="btn.img" v-if="btn.img" />
                    </i>
                    {{btn.text}}
                  </button>
                  </template>
                </template>
                <div v-if="item.operate" style="float: right;margin-right: 1em"><flash></flash></div>
              </div>
                <ul class="form-list detail-list" style="">
                    <li v-for="(items, $indexs) in lists[item.lists.data]" v-if="!items.noShow">
                        <span class="label-span">{{items.name}} :&nbsp;&nbsp;</span>
                        <span class="label-span" :style="{color: items.color || 'auto', 'max-width': 'calc(100% - 8em)'}">{{getValue(items.value)}}</span>
                    </li>
                </ul>
            </div>
        </VuePerfectScrollbar>
      <dialogs type="loading" ref="loadings"></dialogs>
      <dialogs type="alert" ref="alerts"></dialogs>
    </div>
</template>
<script>
    import '../../../../dist/plugin/bgsocket/bg.jssocket.js'
    import flash from  '../../../component/childComponent/FlashPlugin.vue'
    import 'vue-awesome/icons/ticket'
    import 'vue-awesome/icons/reply'
    import 'vue-awesome/icons/credit-card-alt'
    export default{
        data() {
            return {
                paymentList: '',
                show: true,
                selected: '',
                accountInfo: '',
                showPrice: false,
                customerNo: '',
                formOpt: [
                    {
                        name: '用户信息',
                        img: 'dist/img/costControl/user-infor-icon.png',
                        lists: {data: 'customerInfo', isShow: true}
                    },
                    {
                        name: '缴费信息',
                        img: 'dist/img/costControl/Pay-list-icon.png',
                        operate: [
                            {
                                text: '打印小票',
                                event: 'printBill',
                                controls: -100,
                                //img: 'dist/img/costControl/print-ticket-icon.png',
                                icon: 'ticket'
                             },
                           /* {
                                text: '撤销记录',
                                event: 'cancelPayments',
                                controls: 18,
                                //img: 'dist/img/costControl/Revoke-icon.png'
                                icon: 'reply'
                            },
                            {
                                text: '补售电卡',
                                event: 'reAccount',
                                controls: 15,
                               // img: 'dist/img/costControl/Fill-card-icon.png'
                              icon: 'credit-card-alt'
                            }*/
                        ],
                        lists: {data: 'payInfo', isShow: true}
                    }
                ]
            }
        },
        components: {
            flash
        },
        props: ['row', 'controls'],
        methods: {
            printBill: function () {

                this.printPay();
            },
            reAccount: function () {
                this.$parent.openCard(this.selected, 2)
            },
            getStatus: function (menuType, val) {
                let objItem = null;
                let thisObj = null;
                let chargeState = [{text: '全部状态', value: ''},
                    {text: '充值成功', value: '1'},
                    {text: '正在远程充值', value: '-2'},
                    {text: '已取消远程充值', value: '2'},
                    {text: '写卡失败', value: '-1'}];
                switch (menuType) {
                    case 'chargeState': //充值状态
                        thisObj = chargeState;
                        break;
                }
                if (thisObj !== null && typeof val !== 'undefined') {
                    thisObj.forEach(function (item) {
                        if (item.value === val) {
                            objItem = item;
                        }
                    });
                    if (objItem !== null) {
                        return {text: objItem.text, obj: objItem};
                    }
                } else if (thisObj !== null && typeof val === 'undefined') {
                    return {text: "", obj: thisObj};
                }
                return {text: "", obj: objItem};
            },
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            },
            btnEvent: function (eventName) {
                this[eventName]();
            },
            cancelPayments: function () {
                 this.$parent.cancelPayment(this.selected)
            },
            getInfoData: function () {
                let param = {};
                param['payGuid'] = this.selected.payGuid;
                let that = this;
                that.getRequest({
                    url: 'pay/powerPurchaseDetails',
                    method: 'post',
//                    method: 'get',
//                    url: 'dist/json/paymentDetail.json',
                    param: param,
                    success: function (data) {
                      that.paymentList = data.data;
                    },
                    error: function () {
                      that.paymentList = {}
                    }
                }, true)
            },
            init: function () {
                this.selected = this.row;
                this.getInfoData();
            },
            //flash的成功、失败回调函数
            bgFlshCallback: function (msg) {
                this.loadings.close();
                if (typeof  msg !== 'undefined' && msg !== '') {
                    this.alerts.show({msg: msg});
                }
            },
            //打印购电数据
            printPay: function () {
                let that=this;
                this.loadings.show({
                    msg: '正在打印小票,请稍后...'
                });
                this.print({
                    title: "充值凭证",
                    printContent: this.printVal,
                    callback: this.bgFlshCallback,
                    success:function(){
                        that.loadings.close();
                    }
                });
            },
        },
        computed: {
            alerts: function () {
                return this.$refs.alerts
            },
            loadings: function () {
                return this.$refs.loadings
            },
            lists: function () {
                    return {
                        customerInfo: [
                            {name: '用户名称', value: this.paymentList.customerName || ''},
                            {name: '门牌编号', value: this.paymentList.propertyName || ''},
                            {name: '联系电话', value: this.paymentList.customerContact || ''},
                            {name: '透支金额', value: typeof this.paymentList.overdraftAmount == 'undefined' ? '--' : this.paymentList.overdraftAmount.toString()},
                            {name: '告警阀值1', value: this.paymentList.alarmThreshold || '0'},
                            {name: '告警阀值2', value: this.paymentList.alarmThreshold1 || '0'},
                            {name: '告警阀值3', value: this.paymentList.alarmThreshold2 || '0'},
                            {name: "用户状态", value: this.paymentList.isEnabled? (this.paymentList.isEnabled == 1 ? '正常' : '注销') : ''},
                            {name: '用户地址', value: this.paymentList.customerAddr || ''}
                        ],
                        payInfo: [
                            {name: '缴费金额', value: this.paymentList.payment || '', color: '#F3A952'},
                            {name: '充值金额', value: this.paymentList.payMoney || '', color: '#EB4954'},
                            {name: '充值次数', value: this.paymentList.payCount || ''},
                            {name: '充值日期', value: this.paymentList.payDate || ''} ,
                            {name: '记录状态', value: this.paymentList.isValid === 0 ? '已撤销': (this.paymentList.isValid === 1 ? '正常' : '--'), color: '#32DA85'},
                            {name: '撤销人员', value: this.paymentList.cancelUserName || '', noShow: this.paymentList.isValid !== 0},
                            {name: '撤销原因', value: this.paymentList.reason || '', color: '#EB4954', noShow: this.paymentList.isValid !== 0},
                            {name: '撤销时间', value: this.paymentList.cancelTime || '', noShow: this.paymentList.isValid !== 0}
                        ]
                    }
            },
            //打印数据
            printVal: function () {
                let cstName=this.paymentList.customerName;
                let strTime=this.paymentList.payDate;
              //  let deviceNo=this.paymentList.cno;
                if(cstName.length>2){
                    cstName= this.paymentList.customerName.replace(/^(.).+(.)$/g, "$1**$2")
                }
                else{
                    cstName= this.paymentList.customerName.replace(/^(.).+$/g, "$1*")
                }
                if(strTime.length>0){
                    strTime=strTime.substring(0,16)
                }
                // if(deviceNo.length>0){
                //     deviceNo=deviceNo.substring(deviceNo.length-12,deviceNo.length)
                // }
               
                return [
                    {ItemType: 0, Filed: "用户名称:", Value:cstName , Align: 0},
                    // {ItemType: 0, Filed: "表计户号:", Value: this.paymentList.meterUserNo == '' ? this.row.meterUserNo :this.paymentList.meterUserNo , Align: 0},
                    // {ItemType: 0, Filed: "电表表号:", Value: deviceNo, Align: 0},
                    {ItemType: 0, Filed: "用户地址:", Value: this.paymentList.customerAddr, Align: 0},
                    {ItemType: 0, Filed: "充值金额:", Value: "￥" + parseFloat(this.paymentList.payMoney).toFixed(2), Align:0},
                    {ItemType: 0, Filed: "缴费方式:", Value: this.paymentList.payMethod, Align: 0},
                    {ItemType: 0, Filed: "购电日期:", Value: strTime, Align: 0},
                    {ItemType: 0, Filed: "售 电 员:", Value: this.paymentList.chargeUserName.substring(0,1) , Align: 0},
                    {ItemType: 0, Filed: "流 水 号:", Value: this.paymentList.serialNum, Align: 0}
                ]
            }
        },
        mounted: function () {
            this.isIn();
            this.init();
            // this.getAction();
        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
    button.cardOperate {
        padding:5px 12px;
        margin:5px 10px;
        float:left
    }
</style>



