<template>
    <div ref="trys" style="height: calc(100% - 0em)">
        <!-- <div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls"></info>
        </div> -->
        <div v-show="!showInfo" class="all-container ">
          <div class="operate-div" ref="operateDiv" style="margin-right:0">
            <template v-for="(item, $index) in FormOpt">
                <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" style="margin-right:0px" @getInput="getFrom"></inputs>
              
                <datePicker ref="timer" style="margin-left:10px" v-if="item.type == 'timer'"     type= "doubleDate"  :dateOpt="item.options"
                       @getDatePicker="getFrom"></datePicker>
            </template>
          </div>
          <div ref="myOwnTable" class="table-container" :style="{height:'calc(100% - 5em)'}">
            <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding" v-if="tableShow"
                    @rowDbClick="clickTableRow"></tables>
          </div>
        </div>
        <dialogs type="alert" ref="alerts"></dialogs>
    </div>
</template>
<script>
    import info from './Info.vue'
    export default {
        data() {
            return {
                tableShow: false,
                clickEvent: true, //禁止重复提交数据
                row: '', //选中的行数据。。向详情传递
                showInfo: false, //详情延迟加载
                controls: [],
                deepSearchData: '',
                searchParam: {
                  startDate: '',
                  endDate: '',
                  meterUserNo: '',
                  deviceNo: '',
                  customerName: '',
                  customerContact: '',
                  customerAddr: '',
                  deviceType: '07'
                },
                returnDate: '',
                TableOpt: {
                    column: [
                        {title: '记录状态', field: 'isValid', textAlign: 'center', width: '80',   type:"string", formatter: function (val) {
                            if(val == 0){
                                return '<span class="red">失败</span>';}
                            else{
                                return '<span>正常</span>';}
                        }},
                        {title: '用户姓名', field: 'customerName', textAlign: 'center', width: '120'},
                      //  {title: '表号', field: 'deviceNo', textAlign: 'center', width: '120'},
                     //   {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '120'},
                        {title: '退费金额', field: 'refundMoney', textAlign: 'center', width: '100'},
                        {title: '账户余额', field: 'afterRemainAmount', textAlign: 'center', width: '120'},
                        {title: '退费方式', field: 'refundMethod', textAlign: 'center', width: '120', type:"string", formatter: function (val){
                              if(val == 1) {
                                 return '现金';
                              }
                              return '--'
                        }},
                     //   {title: '退费原因', field: 'refundReason', textAlign: 'center', width: '140'},
                        {title: '操作人员', field: 'createUserName', textAlign: 'center', width: '100'},
                        {title: '操作时间', field: 'refundDate', textAlign: 'center', width: '160'},
                        {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '140'},
                        {title: '联系电话', field: 'customerContact', textAlign: 'center', width: '120'},
                        {title: '流水号', field: 'serialNum', textAlign: 'center', width: '140'}
                    ],
                    url: 'refund/queryList',
                    method: 'post',
                  //  url: 'dist/json/returnList.json',
                  //  method: 'get',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    queryParam: {deviceType: '07'},
                    showTotal: true,
                    isExport: false,
                    exportURL: 'refund/queryListDown',
                    dataRow: 'list',
                    totalOpt: [
                        {title: '退费金额', field: 'refundMoney', textAlign: 'center', width: '80'}]
                },
                select: ''
            }
        },
        components: {
           // info
        },
        computed: {
            alerts: function () {
                return this.$refs.alerts
            },
            FormOpt: function () {
              let dates = this.initDate(true);
                return [
               
                  {
                    type: 'timer',
                    options: {
                      key: 'returnDate',
                      value: {startDate: dates.start, endDate: dates.end},
                      width: '23em',
                      fontSize: '0.8em'
                    }
                  },
                  {
                    type: 'input',
                    options: {
                      type: 'typeSearch',
                      key: 'searchParam',
                      selectOpt: {
                        width: '7em',
                        cs: 'client',
                        data: [
                       //   {text: '表计户号', value: 'meterUserNo'},
                      //    {text: '用户表号', value: 'deviceNo'},
                          {text: '用户姓名', value: 'customerName'},
                          {text: '联系电话', value: 'customerContact'},
                          {text: '门牌编号', value: 'propertyName'},
                          {text: '用户地址', value: 'customerAddr'}],
                        text: 'text',
                        value: 'value',
                        key: 'searchType'
                      }
                    }
                  }]
            }
        },
        methods: {
          getFrom: function (val) {
            if (val.combine) {
              if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'startDate', 'endDate']);
                this.getOption(val, true);
                this.getParams(1);
              }
            } else {
              this.getOption(val, true);
            }
          },
            //返回到列表
            closeDiv: function () {
              this.showInfo = false;
              this.getParams();
            },
            //双击查看详情
            clickTableRow: function (val) {
                //this.row = val.val;
                //this.showInfo = true;
            },
            //刷新表格数据
            getParams: function (pageNumber) {
              let arr = this.getSearchData(this.searchParam);
              this.getHeight();
              this.searchTable(arr, pageNumber);
            },
            init: function () {
                let that = this;
                let data = that.initDate(true);
                that.TableOpt.queryParam['startDate'] =  that.searchParam.startDate = data.start;
                that.TableOpt.queryParam['endDate'] =  that.searchParam.endDate =  data.end;
                that.TableOpt.isExport = that.controls[6];
                that.getParams(1);
                that.tableShow = true;
               /* window.onresize = function () {
                  that.windowChange();
                }*/
            },
          windowChange: function () {
            let that = this;
            that.getHeight();
              /*window.onresize = function () {
                that.getHeight();
              }*/
            }
        },
        mounted: function () {
          this.isIn();
          this.getAction();
        },
        watch: {
            returnDate: {
              handler(val, oldVal) {
                if (val !== '' && val !== oldVal) {
                  let arr = val.split('至');
                  this.searchParam.startDate = arr[0];
                  this.searchParam.endDate = arr[1];
                  this.getParams(1)
                }
              }
            },
        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";

</style>



